@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;400;500&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* END RESET */

body {
  font-family: 'Work Sans', sans-serif;
  --primary-color: #FFA800;
  --secondary-color: #ffffff;
  --color-white: #F6F6F6;
  --color-black: #000000;
  background-color: #E5E5E5;
}

form {
  display: -webkit-flex;
  display: flex;
  padding-left: 8px;
  padding-bottom: 10px;
  border-bottom: 1px solid #3D3D3D;
}

label, input {
  width: 100%;
}








.submit-button {
  padding: 0;
  background: transparent;
  border: none;
}

.submit-button:hover {
  cursor: pointer;
}

.submit-button:focus {
  outline: none;
}
.container {
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100vh;
  padding: 14px 28px;
  background-color: var(--color-white);
}

@media only screen and (min-width: 600px) {
  .container {
    padding: 34px 48px;
    width: 60%;
    margin: 0 auto;
    max-width: 1220px
  }
}




.doneTasks-wrapper {
  margin-top: 34px; 
}

.doneTasks-title {
  font-size: 24px;
  line-height: 28px;
  color: var(--primary-color)
}

.header {
  padding: 16px 23px;
  color: var(--color-white);
  background-color: var(--primary-color);
}

.header h1 {
  font-weight: 500;
  font-size: 24px;
}
.icon-button {
  padding: 0;
  background: transparent;
  border: none;
  margin-right: 16px;
}

.icon-button:hover {
  cursor: pointer;
}

.icon-button:focus {
  outline: none;
}

.list-wrapper {
  margin-top: 26px
}

.list-wrapper[data-is-done-tasks="true"] {
  margin-top: 12px
 }

.input-new-task {
  margin-left: 16px;
  line-height: 28px;
  font-size: 24px;
  outline: none;
  border: none;
  color: var(--color-black);
  background-color: transparent;
}

.input-new-task::-webkit-input-placeholder { 
  line-height: 28px;
  color: var(--primary-color);
}

.input-new-task::placeholder { 
  line-height: 28px;
  color: var(--primary-color);
}



.item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 8px;
  margin-bottom: 12px;
  transition: 250ms;
}

.item:hover {
  background-color: rgba(255, 168, 0, 0.2)
}

.cross-task {
  text-decoration: line-through;
  text-decoration-thickness: 1px;
}

.item span {
  padding: 0;
  margin: 0;
  font-size: 24px;
  line-height: 28px;
  width: 80%;
  cursor: text;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.input-edit-task {
  padding: 0;
  margin: 0;
  font: inherit;
  line-height: inherit;
  font-size: 24px;
  outline: none;
  border: none;
  color: var(--color-black);
  background-color: transparent;
}

.input-edit-task::-webkit-input-placeholder { 
  line-height: 28px;
  color: var(--primary-color);
}

.input-edit-task::placeholder { 
  line-height: 28px;
  color: var(--primary-color);
}



